.app {
  --bg-color-1: #9db3ed;
  --bg-color-2: #f9f9f9;
  --text-color-1: #0b1537;
  --text-color-2: #636363;
}

.app[data-theme="dark"] {
  --bg-color-1: #172d67;
  --bg-color-2: #050c26;
  --text-color-1: #fbfcfc;
  --text-color-2: #bbbcbc;
}

.custom-scroll::-webkit-scrollbar{
  width: 5px;
  height: 5px;
}

.custom-scroll::-webkit-scrollbar-track{
  background-color: transparent;
}

.custom-scroll::-webkit-scrollbar-thumb{
  background-color: rgba(0, 0, 0, 0.14);
}

.custom-scroll::-webkit-scrollbar-thumb:hover{
  background-color: gray;
}

.app_calculator{
  height: 500px;
  width: 280px;
  border-radius: 30px;
  box-shadow: 1px -1px 20px rgba(0, 0, 0, 0.15);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.app_calculator_nav{
  position: absolute;
  top: 0;
  left: 0;
  background-color: transparent;
  min-height: 30px;
  padding: 10px;
  overflow: hidden;
  width: 100%;
  min-height: 40px;
  gap: 10px;
  display: flex;
  align-items: center;
}

.app_calculator_nav img{
  height: 24px;
  width: 24px;
}

.app_calculator_nav_toggle{
  height: 15px;
  width: 30px;
  border-radius: 20px;
  box-shadow: 1px 1px 40px 80px rgba(0, 0, 0, 0.14);
}


.app_calculator_nav_toggle_circle{
  height: 15px;
  width: 15px;
  border-radius: 50%;
  background-color: #fff;
  transition: 300ms;
}

.app_calculator_nav_toggle_circle_active{
  margin-left: 15px;
}